.home {
    height: 100vh;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

.sidebar {
    background-color: rgba(27, 26, 26, 0.952);
    height: 90vh;
    border-bottom-left-radius: 40% 47%;
    border-bottom-right-radius: 40% 47%;
}

.items {
    padding-top: 90px;
    width: 60vw;
    display: flex;
    font-size: 25px;
    padding-left: 90px;
}

.items>p {
    padding-left: 90px;
}

.items>p {
    color: white;
}

.top {
    display: flex;
}

.heading {
    padding-top: 50px;
    padding-left: 150px;
    color: white;
}

.heading>h1 {
    font-size: 80px;
    text-decoration: underline 2px solid;
}

.sp {
    color: yellow;
}